body {
  color: #000;
  background: #fff;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

.out {
  display: flex;
  height: 100vh;
}

@media only screen and (max-width: 1080px) {
  .out {
    flex-direction: column;
  }
}

.out__aside {
  flex: 0 0 auto;
  width: 275px;
  background: #343B43;
}

@media only screen and (max-width: 1080px) {
  .out__aside {
    display: none;
  }
}

.out__container {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
}

.main {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  background: url("../img/Working-Environment.jpg") no-repeat center center;
  background-size: cover;
}

.main_simple {
  background: #fff;
}

.inner {
  position: relative;
  flex: 1 1 auto;
  overflow: auto;
  padding: 16px;
  background: rgba(60, 67, 77, 0.6);
}

.inner_simple {
  background: none;
}

.inner_darken {
  background: rgba(60, 67, 77, 0.9);
}

.inner_center {
  text-align: center;
  font-size: 0;
}

.inner_center:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.inner_center .container {
  display: inline-block;
  vertical-align: middle;
  width: 100%;
  text-align: left;
}

.link-primary,
.link-secondary,
.link-approve,
.link-decline {
  text-decoration: none;
}

.link-primary:hover,
.link-secondary:hover,
.link-approve:hover,
.link-decline:hover {
  text-decoration: underline;
}

.link-primary {
  color: #3185D0;
}

.link-secondary {
  color: #20B3F3;
}

.link-approve {
  color: #4a9638;
}

.link-decline {
  color: #eb0000;
}

.checkbox {
  position: relative;
  display: inline-block;
  vertical-align: top;
  min-width: 20px;
  min-height: 20px;
  user-select: none;
  cursor: pointer;
}

.checkbox__input {
  display: none;
}

.checkbox__input:checked ~ .checkbox__icon .checkbox__icon-in {
  opacity: 1;
}

.checkbox__icon {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #9C9C9C;
  border-radius: 2px;
  width: 20px;
  height: 20px;
}

.checkbox__icon-in {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -7px 0 0 -7px;
  opacity: 0;
  font-size: 14px;
  transition: opacity 0.25s;
}

.checkbox__text {
  display: inline-block;
  vertical-align: top;
  padding-left: 32px;
  color: #000;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: 0.02em;
}

.btn {
  padding: 0 10px;
  border: 1px solid #134580;
  border-radius: 2px;
  min-width: 150px;
  color: #fff;
  background: #1B579E;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: inherit;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 53px;
  text-align: center;
}

.btn_secondary {
  border-color: #357EC0;
  background: #20B3F3;
}

.btn_primary {
  border-color: #105897;
  background: #3185D0;
}

.btn_white {
  border-color: #A6A6A6;
  color: #3F3F3F;
  background: #fff;
  box-shadow: none;
}

.btn_sm {
  min-width: 125px;
  height: 45px;
  font-size: 18px;
  font-weight: 500;
  line-height: 43px;
  letter-spacing: 0.02em;
  box-shadow: none;
}

.btn_lg {
  height: 61px;
  line-height: 59px;
}

.btn-add {
  position: relative;
  padding: 10px 2px 10px 40px;
  color: #20B3F3;
  background: none;
  font-size: 0;
}

.btn-add__icon {
  position: absolute;
  top: 50%;
  left: 4px;
  margin-top: -12px;
  border: 2px solid #3185D0;
  border-radius: 50%;
  width: 24px;
  height: 24px;
}

.btn-add__icon:before, .btn-add__icon:after {
  content: '';
  position: absolute;
}

.btn-add__icon:before {
  top: 3px;
  left: 50%;
  bottom: 3px;
  margin-left: -1px;
  border-left: 2px solid #3185D0;
}

.btn-add__icon:after {
  top: 50%;
  left: 3px;
  right: 3px;
  margin-top: -1px;
  border-top: 2px solid #3185D0;
}

.btn-add__text {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.switcher {
  display: inline-block;
  vertical-align: top;
  font-size: 0;
  cursor: pointer;
}

.switcher__input {
  display: none;
}

.switcher__input:checked ~ .switcher__icon:after {
  transform: translateX(20px);
}

.switcher__icon {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 40px;
  height: 20px;
}

.switcher__icon:before, .switcher__icon:after {
  content: '';
  position: absolute;
  top: 50%;
  background: #3185D0;
}

.switcher__icon:before {
  left: 3px;
  right: 3px;
  margin-top: -7px;
  border-radius: 7px;
  height: 14px;
  opacity: 0.5;
}

.switcher__icon:after {
  left: 0;
  margin-top: -10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
  transition: transform 0.25s;
}

.switcher__text {
  display: inline-block;
  vertical-align: top;
  font-family: inherit;
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.25;
  letter-spacing: 0.02em;
}

.switcher__right {
  margin-left: 10px;
}

.switcher__left {
  margin-right: 10px;
}

.select {
  display: inline-block;
  vertical-align: top;
  padding: 0 16px;
  border: 1px solid #9C9C9C;
  border-radius: 2px;
  width: 100%;
  height: 50px;
  background-color: #fff;
  background-image: url("../img/angle-down.svg");
  background-repeat: no-repeat;
  background-position: right 15px center;
  background-size: auto 18px;
  font-family: inherit;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.02em;
  appearance: none;
}

.select::ms-expand {
  display: none;
}

.select_lg {
  height: 61px;
  font-size: 20px;
}

.input {
  display: inline-block;
  vertical-align: top;
  padding: 0 16px;
  border: 1px solid #9C9C9C;
  border-radius: 2px;
  width: 100%;
  height: 50px;
  background-color: #fff;
  font-family: inherit;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.02em;
  appearance: none;
}

.input_sm {
  padding: 0 20px;
  height: 40px;
  font-size: 18px;
}

.input_sm-width {
  max-width: 210px;
}

.input[readonly] {
  border-color: #A6A6A6;
  color: rgba(0, 0, 0, 0.42);
  background: #E3E3E3;
}

.message {
  color: #1F7212;
  padding: 7px 0;
  font-size: 18px;
  font-style: normal;
  font-weight: normal;
  line-height: 1.25;
  text-align: center;
  letter-spacing: 0.02em;
}

.password-box {
  position: relative;
}

.password-box__icon {
  position: absolute;
  top: 50%;
  right: 15px;
  margin-top: -13px;
  width: 26px;
  height: 26px;
  background: url("../img/fingerprint.svg") no-repeat center center;
  background-size: contain;
}

.password-box__input {
  padding-right: 56px;
}

.calendar-box {
  position: relative;
}

.calendar-box__icon {
  position: absolute;
  top: 50%;
  right: 13px;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  background: url("../img/calendar.png") no-repeat center center;
  background-size: contain;
}

.calendar-box__input {
  padding-right: 63px;
}

.field__label {
  display: block;
  padding: 8px 0;
  color: #000;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.field__label_lg {
  font-size: 18px;
  text-transform: none;
}

.field__label_simple {
  text-transform: none;
}

.field__message {
  margin-top: 3px;
  text-align: center;
}

.alert {
  position: relative;
  padding: 9px 20px;
  display: block;
  margin: 0 auto;
  width: 374px;
  max-width: 100%;
  border-radius: 6px;
  color: #fff;
  background: #6CBB5F;
  font-family: inherit;
  font-size: 18px;
  line-height: 1.25;
  text-align: center;
  letter-spacing: 0.02em;
}

.alert:not(:first-child) {
  margin-top: 21px;
}

.alert__close {
  position: absolute;
  top: -9px;
  right: -12px;
  width: 25px;
  height: 25px;
  color: #000;
  background: none;
  text-align: center;
  font-size: 0;
}

.alert__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.alert__icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 17px;
}

.header {
  flex: 0 0 auto;
  padding: 10px 16px;
  min-height: 70px;
  background: #3185D0;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__nav-btn {
  position: relative;
  float: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 7px;
  width: 50px;
  height: 50px;
  background: none;
}

.header__nav-btn:before, .header__nav-btn:after,
.header__nav-btn span {
  display: block;
  border-radius: 3px;
  width: 36px;
  height: 6px;
  background: #fff;
}

.header__nav-btn:before, .header__nav-btn:after {
