}

.header__title {
  padding: 13px 0;
  color: #fff;
  font-size: 24px;
  font-style: normal;
  font-weight: normal;
  text-align: center;
  line-height: 1;
  letter-spacing: 0.02em;
}

.menu__header {
  padding: 15px;
  margin-right: 3px;
  color: #fff;
