.information {
	display: flex;
	border: 1px solid #D6D6D6;
	color: #3C434D;
	background: $white;
	@include sm {
		display: block; }
	&__status,
	&__box {
		flex: 1 1 50%;
		width: 50%;
		@include sm {
			width: 100%; } }
	&__box {
		padding: 20px 15px 37px; }
	&__status {
		position: relative;
		padding: 15px;
		@include sm {
			padding: 10px 0; }
		&:not(:last-child) {
			&:before {
				content: '';
				position: absolute;
				top: 32px;
				right: 0;
				bottom: 32px;
				border-right: 1px solid $black;
				@include sm {
					top: auto;
					left: 32px;
					right: 32px;
					bottom: 0;
					border: 0;
					border-bottom: 1px solid $black; } } } }
	&__title {
		padding: 15px;
		font-weight: 500;
		font-size: 26px;
		line-height: 1; }
	&__value,
	&__label,
	&__subtitle {
		font-size: 22px;
		line-height: 1;
		@include lg {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } }
	&__value {
		flex: 1 1 auto;
		&-item {
			&:not(:last-child) {
				margin-bottom: 20px; } } }
	&__label {
		flex: 0 0 auto;
		padding-right: 30px; }
	&__text {
		display: flex;
		padding: 15px; }
	&__subtitle {
		margin-bottom: 17px;
		text-align: center; }
	&__row {
		margin: 0 auto;
		max-width: 339px;
		display: flex;
		justify-content: space-between;
		@include xs {
			display: block;
			max-width: none; } }
	&__col {
		@include xs {
			&:not(:last-child) {
				margin-bottom: 15px; } } } }
