.header {
	flex: 0 0 auto;
	padding: 10px 16px;
	min-height: 70px;
	background: $primary;
	@include clr;
	&__nav-btn {
		position: relative;
		float: left;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 7px;
		width: 50px;
		height: 50px;
		background: none;
		&:before,
		&:after,
		span {
			display: block;
			border-radius: 3px;
			width: 36px;
			height: 6px;
			background: $white; }
		&:before,
		&:after {
			content: ''; } }
	&__title {
		padding: 13px 0;
		color: $white;
		font-size: 24px;
		font-style: normal;
		font-weight: normal;
		text-align: center;
		line-height: 1;
		letter-spacing: 0.02em; } }

